<template>
  <div class="mt-3">
    <h3 class="text-light mb-3">Opes de capa</h3>
    <div class="form-group">
      <Label for="background" text="Imagem do artefato" />
      <div class="custom-file">
        <input
          type="file"
          class="custom-file-input"
          id="backgroundFile"
          @change="changeBackground($event.target)"
          :multiple="false"
          lang="pt-br"
        />
        <label class="custom-file-label" for="backgroundFile">
          {{ item.file ? item.file.name : '' }}
        </label>
      </div>
    </div>
    <div class="form-group">
      <Label text="Enquadramento" />
      <ImageFraming v-model="itemBackgroundSize" />
      <p class="text-light small mt-2" v-if="itemBackgroundSize === 'cover'">
        Preenche o quadro com a imagem.
      </p>
      <p class="text-light small mt-2" v-if="itemBackgroundSize === 'contain'">
        Encaixa a imagem dentro do quadro.
      </p>
    </div>
    <div class="form-group">
      <Label for="background-axis" text="Posicionamento da imagem" />
      <div class="row" id="background-axis">
        <div class="col">
          <Label
            class="text-center"
            for="background-horizontal-axis"
            text="Horizontal"
          />
          <input
            class="custom-range"
            type="range"
            min="0"
            max="100"
            step="1"
            id="background-horizontal-axis"
            v-model="itemBackgroundPositionX"
          />
        </div>
        <div class="col">
          <Label
            class="text-center"
            for="background-vertical-axis"
            text="Vertical"
          />
          <input
            class="custom-range"
            type="range"
            min="0"
            max="100"
            step="1"
            id="background-vertical-axis"
            v-model="itemBackgroundPositionY"
          />
        </div>
      </div>
    </div>
    <div class="form-group">
      <Label for="itemBackgroundColor" text="Cor de fundo" />
      <Verte v-model="itemBackgroundColor" display="widget" />
    </div>
  </div>
</template>

<script>
  import { mapMutations, mapState } from 'vuex';
  import { mapFields } from 'vuex-map-fields';
  import Verte from 'verte';
  import 'verte/dist/verte.css';

  import ImageFraming from './ImageFraming.vue';
  import Label from './Label.vue';

  export default {
    name: 'ItemCoverOptions',
    components: {
      ImageFraming,
      Label,
      Verte
    },
    computed: {
      ...mapState([
        'item',
        'rarities',
        'types',
      ]),
      ...mapFields({
        itemBackgroundColor: 'item.backgroundColor',
        itemBackgroundPositionX: 'item.backgroundPositionX',
        itemBackgroundPositionY: 'item.backgroundPositionY',
        itemBackgroundSize: 'item.backgroundSize'
      })
    },
    methods: {
      ...mapMutations({
        setItemFile: 'SET_ITEM_FILE'
      }),
      changeBackground ({ files }) {
        this.setItemFile(files[0]);
      }
    }
  }
</script>

<style lang="scss" scoped>
  .custom-file-label::after {
    content: "Selecionar" !important;
  }
</style>