<template>
  <div class="generated-items">
    <figure
      class="figure generated-item"
      v-for="(item, uuid) in generatedItems"
      :key="uuid"
      :title="item.name"
    >
      <figcaption class="figure-caption text-truncate">
        {{ item.name }}
      </figcaption>
      <div class="figure-image" @click="download(item.image)">
        <img
          class="img-fluid rounded"
          :src="item.image.url"
          :alt="item.image.name"
        />
      </div>
      <figcaption class="figure-caption">
        <div class="row justify-content-between">
          <div class="col">
            <a class="text-dark" @click="editGeneratedItem(uuid)">
              <i class="fa fa-edit"></i> Editar
            </a>
          </div>
          <div class="col">
            <a class="text-danger" @click="removeGeneratedItem(uuid)">
              <i class="fa fa-trash"></i> Remover
            </a>
          </div>
        </div>
      </figcaption>
    </figure>
  </div>
</template>

<script>
  import { mapActions, mapMutations } from 'vuex';

  export default {
    name: 'GeneratedItemsList',
    props: {
      generatedItems: Object
    },
    computed: {
      hasGeneratedItems () {
        return !!Object.keys(this.generatedItems).length;
      }
    },
    methods: {
      ...mapActions([
        'editGeneratedItem'
      ]),
      ...mapMutations({
        removeGeneratedItem: 'REMOVE_GENERATED_ITEM',
      }),
      download (image) {
        const a = document.createElement('a');
        a.href = image.url.replace('image/jpeg', 'image/octet-stream');
        a.download = image.name;
        a.click();
      }
    }
  }
</script>

