import Vue from 'vue';
import Vuex from 'vuex';
import Uuid from 'uuid/v4';
import { getField, updateField } from 'vuex-map-fields';
import swal from 'sweetalert';

Vue.use(Vuex);

const rarities = [
  'Comum',
  'Incomum',
  'Raro',
  'Muito Raro',
  'Lendrio',
  'Artefato',
  'nico',
];

const types = [
  'Anel',
  'Arma',
  'Armadura',
  'Beno',
  'Cajado',
  'Item Maravilhoso',
  'Pergaminho',
  'Poo',
  'Varinha',
];

const initialItemData = {
  uuid: null,
  name: 'Meu novo item mgico',
  type: types[4],
  subType: '',
  rarity: rarities[0],
  description: '',
  effects: '',
  requiresAttunement: false,
  file: null,
  backgroundColor: '#ffffff',
  backgroundSize: 'contain',
  backgroundPositionX: 50,
  backgroundPositionY: 50,
  showLevels: true
};

export default new Vuex.Store({
  state: {
    timeoutId: null,
    generatedItems: {},
    item: { ...initialItemData },
    rarities,
    types
  },
  getters: {
    getField,
  },
  mutations: {
    updateField (state, payload) {
      clearTimeout(state.timeoutId);

      state.timeoutId = setTimeout(() => updateField(state, payload), 100);
    },
    SET_GENERATED_ITEM (state, image) {
      const uuid = state.item.uuid || Uuid();

      Vue.set(state.generatedItems, uuid, {
        ...state.item,
        uuid,
        image
      });
    },
    async REMOVE_GENERATED_ITEM (state, uuid) {
      const shouldRemove = await swal({
        title: 'Deseja destruir este artefato?',
        text: 'Todos seus componentes sero perdidos para sempre!',
        icon: 'warning',
        dangerMode: true,
        buttons: ['Cancelar', 'Descartar artefato']
      })

      if (shouldRemove) {
        Vue.delete(state.generatedItems, uuid);
      }
    },
    async REMOVE_GENERATED_ITEMS (state) {
      const shouldRemove = await swal({
        title: 'Deseja destruir todos os artefatos?',
        text: 'Isso pode abalar o equilibrio mgico da regio!',
        icon: 'warning',
        dangerMode: true,
        buttons: ['Cancelar', 'Destruir artefatos']
      })

      if (shouldRemove) {
        state.generatedItems = {};
      }
    },
    SET_ITEM_FILE (state, file) {
      Vue.set(state.item, 'file', file);
    },
    SET_ITEM_DATA (state, item) {
      Vue.set(state, 'item', { ...item });
    },
    RESET_ITEM_DATA (state) {
      state.item = initialItemData;
    }
  },
  actions: {
    editGeneratedItem ({ commit, state: { generatedItems } }, uuid) {
      commit('SET_ITEM_DATA', generatedItems[uuid]);
    },
    setGeneratedItem ({ commit }, image) {
      commit('SET_GENERATED_ITEM', image);
      commit('RESET_ITEM_DATA');
    }
  }
})
