<template functional>
  <div class="btn-group btn-group-toggle w-100">
    <label
      class="btn btn-light w-50"
      v-for="option in props.options"
      :key="option.type"
      :class="{ active: props.value === option.type }"
    >
      <input
        type="radio"
        name="framing"
        :id="`framing-${option.type}`"
        autocomplete="off"
        :checked="props.value === option.type"
        @change="listeners.input($event.target.value)"
        :value="option.type"
      /> 
      <i class="fa fa-check-circle" v-if="props.value === option.type"></i> 
      <span>{{ option.name }}</span>
    </label>
  </div>
</template>

<script>
  export default {
    name: 'ImageFraming',
    functional: true,
    props: {
      value: String,
      options: {
        type: Array,
        default: () => [
          { type: 'cover', name: 'Cortar' },
          { type: 'contain', name: 'Encaixar' }
        ]
      }
    }
  }
</script>