<script>
  import marked from 'marked';

  export default {
    name: 'BackgroundCard',
    props: {
      item: {
        type: Object
      }
    },
    computed: {
      itemDescription () {
        return this.getMarkdownText(this.item.description);
      },
      itemEffects () {
        return this.getMarkdownText(this.item.effects);
      }
    },
    methods: {
      getMarkdownText (text) {
        return marked(text, { sanitize: true });
      }
    }
  }
</script>

