<template>
  <div class="item-preview">
    <div class="d-flex justify-content-center align-items-center h-100">
      <div
        class="item-cards"
        :class="[previewSize]"
        ref="itemCards"
      >
        <ItemCard class="gold-card">
          <BackgroundCard :item="item" />
        </ItemCard>
        <ItemCard class="gold-card">
          <FrontCard :item="item" />
        </ItemCard>
      </div>
    </div>
    <div class="generate-options">
      <div class="form-inline">
        <label class="my-1 mx-1" for="itemName">
          <strong>Nome do artefato:</strong>
        </label>
        <input
          class="form-control mx-1 my-1"
          type="text"
          id="itemName"
          v-model="itemName"
        />
        <button
          class="btn btn-dark text-left text-light"
          type="button"
          @click="generateImage"
        >
          <i class="ra ra-fairy-wand mr-2"></i>
          <strong v-if="item.uuid">Reconstruir item mgico</strong>
          <strong v-else>Criar item mgico</strong>
        </button>
      </div>
      <div class="form-inline">
        <label class="my-1 mx-1" for="itemName">
          <strong>Tamanho de visualizao:</strong>
        </label>
        <div class="btn-group">
          <button
            class="btn btn-outline-secondary btn-sm"
            :class="{ active: scale === 'sm' }"
            @click="toggleSize('sm')"
            title="Torna o preview mais fiel ao resultado final"
          >
            Pequeno
          </button>
          <button
            class="btn btn-outline-secondary btn-sm"
            :class="{ active: scale === 'md' }"
            @click="toggleSize('md')"
            title="Tamanho de visualizao padro"
          >
            Normal
          </button>
          <button
            class="btn btn-outline-secondary btn-sm"
            :class="{ active: scale === 'lg' }"
            @click="toggleSize('lg')"
            title="Aumenta o tamanho do preview"
          >
            Grande
          </button>
        </div>
      </div>
    </div>
    <div class="zoom-options">
    </div>
  </div>
</template>

<script>
  import { mapState, mapActions } from 'vuex';
  import { mapFields } from 'vuex-map-fields';

  import BackgroundCard from './BackgroundCard.vue';
  import FrontCard from './FrontCard.vue';
  import ItemCard from './ItemCard.vue';

  export default {
    name: 'ItemPreview',
    components: {
      BackgroundCard,
      FrontCard,
      ItemCard
    },
    data () {
      return {
        scale: 'md'
      }
    },
    computed: {
      ...mapState([ 'item' ]),
      ...mapFields({
        itemName: 'item.name'
      }),
      previewSize () {
        return  `item-preview-${this.scale}`;
      }
    },
    methods: {
      ...mapActions([ 'setGeneratedItem' ]),
      async exportItem () {
        const el = this.$refs.itemCards;

        return await this.$html2canvas(el);
      },
      async generateImage () {
        const canvas = await this.exportItem();

        this.setGeneratedItem({
          name: `${this.item.name}.jpg`,
          url: canvas.toDataURL('image/jpeg'),
          width: canvas.width,
          height: canvas.height
        });
      },
      toggleSize (value) {
        this.scale = value;
      }
    }
  }
</script>

<style lang="scss" scoped>
  .item-preview {
    height: 100%;
    max-height: 100%;
    max-width: 100%;
    width: 100%;

    $transition: transform 1s ease-in-out;

    $maxWidth: 1500px;

    .item-cards {
      display: flex;
      margin: 4rem auto 0 auto;
      max-width: $maxWidth;
      overflow: hidden;
      position: relative;
      width: $maxWidth;
    }

    .generate-options {
      background-color: rgba(white, .95);
      display: flex;
      height: auto;
      justify-content: space-between;
      left: 0;
      padding: .5rem;
      position: absolute;
      top: 0;
      width: 100%;
    }

    .zoom-options {
      bottom: 1rem;
      display: flex;
      flex-direction: column;
      height: auto;
      justify-content: center;
      right: 1rem;
      padding: .5rem;
      position: absolute;
      width: auto;
    }

    .item-preview-sm {
      zoom: 0.33;
    }

    .item-preview-md {
      zoom: 0.45;
    }

    .item-preview-lg {
      zoom: 0.63;
    }
  }
</style>