<template>
    <div class="row generated-items">
      <div class="col-10">
        <GeneratedItemsList :generated-items="generatedItems" />
      </div>
      <div class="col-2 border-left bg-light">
        <div class="h-100 d-flex flex-column justify-content-center align-items-center">
          <h4 class="text-dark">Exportar artefatos</h4>
          <div class="w-75">
            <div class="input-group input-group-sm mb-2">
              <select
                class="form-control"
                name="export-format"
                id="export-format"
                v-model="format"
              >
                <option v-for="(format, key) in formats" :key="key" :value="key">
                  {{ key }}
                </option>
              </select>
              <select
                class="form-control"
                name="export-orientation"
                id="export-orientation"
                v-model="orientation"
              >
                <option v-for="(value, key) in orientations" :key="key" :value="key">
                  {{ value }}
                </option>
              </select>
            </div>
            <button
              class="btn btn-dark btn-block"
              type="button"
              @click="exportToPdf"
              :disabled="!hasGeneratedItems"
            >
              <strong>Exportar</strong>
            </button>
            <button
              class="btn btn-outline-dark btn-sm btn-block"
              type="button"
              @click="removeGeneratedItems"
              :hidden="!hasGeneratedItems"
            >
              <i class="fa fa-trash mr-2"></i> Remover artefatos
            </button>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  import { mapState, mapMutations } from 'vuex';
  import jsPDF from 'jspdf';

  import GeneratedItemsList from './GeneratedItemsList.vue';

  export default {
    name: 'GeneratedItems',
    components: {
      GeneratedItemsList
    },
    data () {
      const margin = 10;

      const getFormat = (width, height) => ({
        portrait: {
          height,
          width,
          availableHeight: (height - (margin * 2)),
          availableWidth: (width - (margin * 2))
        },
        landscape: {
          height: width,
          width: height,
          availableHeight: (width - (margin * 2)),
          availableWidth: (height - (margin * 2))
        },
      });

      return {
        formats: {
          a5: getFormat(148, 210),
          a4: getFormat(210, 297),
          a3: getFormat(297, 420),
          a2: getFormat(420, 594)
        },
        margin,
        format: 'a4',
        orientation: 'landscape',
        orientations: {
          portrait: 'Retrato',
          landscape: 'Paisagem'
        }
      }
    },
    computed: {
      ...mapState([ 'generatedItems' ]),
      hasGeneratedItems () {
        return this.itemsCount > 0;
      },
      itemsCount () {
        return Object.keys(this.generatedItems).length;
      },
      itemsCountLabel () {
        return this.itemsCount > 1
          ? `${this.itemsCount} itens criados`
          : '1 item criado';
      },
      pageDimensions () {
        const { formats, format, orientation } = this;

        return formats[format][orientation];
      },
    },
    methods: {
      ...mapMutations({
        removeGeneratedItems: 'REMOVE_GENERATED_ITEMS'
      }),
      exportToPdf () {
        if (!this.hasGeneratedItems) return;

        const doc = new jsPDF({
          unit: 'mm',
          format: this.format,
          orientation: this.orientation
        });

        let positionX = this.margin,
          positionY = this.margin;

        for (const uuid in this.generatedItems) {
          const { image } = this.generatedItems[uuid];

          const { width, height } = this.getImageRealSize(image);

          const fitsFormatWidth = this.fitsFormatWidth(positionX + width);

          const fitsFormatHeight = this.fitsFormatHeight(positionY + height);

          if (!fitsFormatWidth && !fitsFormatHeight) {
            doc.addPage(this.format, this.orientation);
            positionX = this.margin;
            positionY = this.margin;
          } else if (!fitsFormatWidth && fitsFormatHeight) {
            positionX = this.margin;
            positionY += (height + this.margin);
          }

          doc.addImage(image.url, 'JPEG', positionX, positionY, width, height);//, null, 'SLOW');

          positionX += (width + this.margin);
        }

        doc.save('export.pdf');
      },
      fitsFormat (dimension, position) {
        return this.pageDimensions[dimension] >= position;
      },
      fitsFormatHeight (position) {
        return this.fitsFormat('availableHeight', position);
      },
      fitsFormatWidth (position) {
        return this.fitsFormat('availableWidth', position);
      },
      getImageRealSize ({ width, height }, dpi = 300) {
        const getCovertedSize = (value) => ((value * 25.4) / dpi);

        return {
          width: getCovertedSize(width),
          height: getCovertedSize(height)
        };
      },
    }
  }
</script>

<style lang="scss" scoped>
  .generated-items {
    background: linear-gradient(to right, rgba(#eee, .85), rgba(#fff, .85)),
                url("/assets/items-background.jpg");
    background-repeat: repeat;
  }

  .w-auto {
    width: auto !important;
  }
</style>